import React, { PureComponent } from 'react';
// import { capitalize } from 'lodash-es';
import tableData from './TableData';
import Sidepane from './Sidepane';
import { animated, config, Transition } from 'react-spring';
import './Sidepane.scss';

export class TableWithSidepane extends PureComponent {
  constructor() {
    super();

    this.state = {
      activeRowIndex: null
    }
  }

  setActiveRowIndex = (rowIndex) => {
    this.setState({
      activeRowIndex: rowIndex
    })
  }

  render() {
    const { activeRowIndex } = this.state;

    return (
      <>
        <table className="u-full-width">
          <thead>
            <tr>
              {tableData.map((row, i) => {
                return (
                  <th key={`table-heading-${i}`}>{Object.keys(row)[i]}</th>
                )
              })}
            </tr>
          </thead>
          <tbody>
            {tableData.map((row, i) => {
              return (
                <tr key={`table-row-${i}`}>
                  <td>{row.name}</td>
                  <td>{row.job}</td>
                  <td><button type="button" onClick={this.setActiveRowIndex.bind(this, i)}>Show more info for {row.name}</button></td>
                </tr>
              )
            })}
          </tbody>
        </table>

        <Sidepane activeRowIndex={activeRowIndex} tableData={tableData} />
      </>
    )
  }
}

